.LandingScreen {
  height: 100vh;
  position: relative; }

.LandingScreen__content {
  padding: 90px 70px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0; }

.LandingScreen__introInfo__title {
  color: #282B2E;
  font-size: 28px;
  line-height: 32px;
  margin-right: -38px; }
  .LandingScreen__introInfo__title span {
    display: block; }

.LandingScreen__introInfo__title__role {
  font-weight: 300;
  font-style: italic; }

.LandingScreen__socialLinks {
  margin-top: 40px; }

.LandingScreen__nextPreview {
  position: absolute;
  right: 70px;
  bottom: 90px; }

@-webkit-keyframes animateDownArrow {
  0% {
    -webkit-transform: translateY(0) scale(0.5);
            transform: translateY(0) scale(0.5);
    opacity: 1; }
  25% {
    -webkit-transform: translateY(12px) scale(1);
            transform: translateY(12px) scale(1); }
  50% { }
  55% {
    opacity: 0.1; }
  60% {
    -webkit-transform: translateY(36px) scale(0.85);
            transform: translateY(36px) scale(0.85);
    opacity: 0; }
  100% {
    -webkit-transform: translateY(36px);
            transform: translateY(36px);
    opacity: 0; } }

@keyframes animateDownArrow {
  0% {
    -webkit-transform: translateY(0) scale(0.5);
            transform: translateY(0) scale(0.5);
    opacity: 1; }
  25% {
    -webkit-transform: translateY(12px) scale(1);
            transform: translateY(12px) scale(1); }
  50% { }
  55% {
    opacity: 0.1; }
  60% {
    -webkit-transform: translateY(36px) scale(0.85);
            transform: translateY(36px) scale(0.85);
    opacity: 0; }
  100% {
    -webkit-transform: translateY(36px);
            transform: translateY(36px);
    opacity: 0; } }

@-webkit-keyframes animateDownArrowNudge {
  0%, 25%, 40%, 85%, 100% {
    -webkit-transform: translateY(0);
            transform: translateY(0); }
  45% {
    -webkit-transform: translateY(2px);
            transform: translateY(2px); } }

@keyframes animateDownArrowNudge {
  0%, 25%, 40%, 85%, 100% {
    -webkit-transform: translateY(0);
            transform: translateY(0); }
  45% {
    -webkit-transform: translateY(2px);
            transform: translateY(2px); } }

.LandingScreen__nextPreview__title {
  font-size: 28px;
  line-height: 32px;
  font-weight: 300;
  letter-spacing: 0.5px;
  cursor: pointer; }
  .LandingScreen__nextPreview__title .LandingScreen__nextPreview__title__icons {
    display: inline-block;
    margin-left: 10px;
    position: relative;
    padding-right: 28px; }
  .LandingScreen__nextPreview__title svg {
    fill: #ACAFB1;
    position: absolute;
    right: 0;
    bottom: -1px;
    -webkit-transition: fill 250ms ease;
    -o-transition: fill 250ms ease;
    transition: fill 250ms ease; }
    .LandingScreen__nextPreview__title svg path {
      fill: inherit; }
  .LandingScreen__nextPreview__title:hover svg {
    fill: rgba(245, 77, 160, 0.64); }

.LandingScreen__nextPreview__title__icons__line path {
  opacity: 0.5;
  -webkit-animation: animateDownArrow 3000ms ease infinite;
          animation: animateDownArrow 3000ms ease infinite; }

.LandingScreen__nextPreview__title__icons__line:hover path {
  stroke-dashoffset: -94px; }

.LandingScreen__nextPreview__title__icons__mainAbove {
  opacity: 0;
  bottom: 12px;
  -webkit-animation: animateDownArrow 3000ms ease infinite;
          animation: animateDownArrow 3000ms ease infinite; }



